<script lang="ts">
import {defineComponent} from 'vue'

export default defineComponent({
  name: "AuthDevice",
  mounted() {
    if (!this.$yin.me._id)
      this.$router.push('/auth')
    else {
      location.replace(`yin-desktop://${JSON.stringify({token: this.$yin.me._token})}`)
      // window.open(`yin-desktop://token=${this.$yin.me._token}`)
      // setTimeout(() => {
      //   this.$router.replace('/')
      // }, 2000)
    }
  },
})
</script>

<template>
  <p>正在跳转</p>
</template>

<style scoped lang="less">
p {
  margin: 55px;
  text-align: center;
}
</style>
